import React, { Component } from 'react';

class About extends React.Component {
  constructor(){
    super();
    this.state = {
      isOpen: false,
    };
  }

  handleAbout() {
    this.state.isOpen === true ? this.setState({isOpen: false}) : this.setState({isOpen: true});
  }

  render() {
    if (this.state.isOpen === true){
      return (
        <div>
        <section class="section" id="about">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border2" onClick={()=>this.handleAbout()}>ABOUT</h2>
              <p class="section-desc"><br />
                阿部 航介<br />
                KOSUKE ABE<br />
                1994.12.1
              </p>
            </div>
            <div class="two-column-wrapper">
              <div class="two-column-image">
                <img src="img/profile/profile.jpg"/>
              </div>
              <div class="two-column-desc">
                <p class="text"><br />
                  2014年4月 慶應義塾大学経済学部入学。<br />
                  2016年4月~2018年2月 デザイン思考を用いて、産学協同で新規サービスの企画を行う。<br />
                  2018年3月 慶應義塾大学経済学部卒業。<br />
                  2018年4月 富士通株式会社にシステムエンジニアとして入社。<br />
                  2018年12月 富士通株式会社を退職。<br />
                  2019年5月 株式会社gifteeにwebエンジニアとして入社。<br />
                  趣味：SF小説執筆、楽器演奏<br />
                  資格：TOEIC 905点</p>
              </div>
            </div>
          </div>
        </section>
        </div>
      );
    }else{
      return(
        <div>
        <section class="section" id="about">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border" onClick={()=>this.handleAbout()}>ABOUT</h2>
            </div>
          </div>
        </section>
        </div>
      )
    }
  }
}

export default About;
