import React, { Component } from 'react';
import MainContainer from "../Components/MainContainer"


class Header extends React.Component {
  constructor(){
    super();
    this.state = {
      isOpen: false,
    };
  }

  handleHeader() {
    this.state.isOpen === true ? this.setState({isOpen: false}) : this.setState({isOpen: true});
  }

  render() {
    if (this.state.isOpen === false){
      return (
        <div className="App">
          <header id="header">
            <h1 class="logo">
            <a class="logo-title" onClick={() => this.handleHeader()}>KOSUKE ABE</a>
            <a class="logo-desc" onClick={() => this.handleHeader()}>Web enginieer, Design thinker</a>
          </h1>
          </header>
        </div>
      );
    }else{
      return (
        <MainContainer />
      )
    }
  }
}

export default Header;
