import React, { Component } from 'react';

class Skill extends React.Component {
  constructor(){
    super();
    this.state = {
      isOpen: false,
    };
  }

  handleSkill() {
    this.state.isOpen === true ? this.setState({isOpen: false}) : this.setState({isOpen: true});
  }

  render() {
    if (this.state.isOpen === true){
      return (
        <div>
        <section class="section" id="skill">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border2" onClick={()=>this.handleSkill()}>TOOLS</h2>
              <p class="section-desc">
              </p>
            </div>
            <div class="card-wrapper">
              <div class="skill-desc">
                <h3 class="skill-desc-title"></h3>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_ruby.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_java.png" alt=""/>
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_htmlcss.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_js.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_react.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_rails.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_git.png" alt="" />
                </div>
              </div>
              <div class="card">
                <div class="skill-icon">
                  <img src="img/skill/icon_illustrator.png" alt="" />
                </div>
              </div>
            </div>
          </div>
        </section>
        </div>
      );
    }else{
      return(
        <div>
        <section class="section" id="about">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border" onClick={()=>this.handleSkill()}>TOOLS</h2>
            </div>
          </div>
        </section>
        </div>
      )
    }
  }
}

export default Skill;
