import React, { Component } from 'react';

class Contact extends React.Component {
  constructor(){
    super();
    this.state = {
      isOpen: false,
    };
  }

  handleContact() {
    this.state.isOpen === true ? this.setState({isOpen: false}) : this.setState({isOpen: true});
  }

  render() {
    if (this.state.isOpen === true){
      return (
        <div>
        <section class="section" id="contact">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border2" onClick={()=>this.handleContact()}>LINK</h2>
              <p class="section-desc">
              </p>
            </div>
            <div class="contact-wrapper">
              <div class="contact-list">
                <ul>
                  <li><a href="https://github.com/KosukeAbe1201" target="_blank"><img src="img/contact/icon_github.png" alt="" /><span class="contact-list-desc"></span></a></li>
                  <li><a href="https://bitbucket.org/memetics1201/" target="_blank"><img src="img/contact/icon_bitbucket.png" alt="" /><span class="contact-list-desc"></span></a></li>
                  <li><a href="https://qiita.com/memetics" target="_blank"><img src="img/contact/icon_qiita.png" alt="" /><span class="contact-list-desc"></span></a></li>
                </ul>
              </div>
            </div>
          </div>
        </section>
        </div>
      );
    }else{
      return(
        <div>
        <section class="section" id="about">
          <div class="inner">
            <div class="section-title-block">
              <h2 class="title-border" onClick={()=>this.handleContact()}>LINK</h2>
            </div>
          </div>
        </section>
        </div>
      )
    }
  }
}

export default Contact;
